	module.exports = {"data":{"site":{"siteMetadata":{"title":"Gatsby Starter - Forty by HTML5 UP","author":"Hunter Chang"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/alternativa3d-more-3d-goodness-from-the-flash-player/index.md absPath of file >>> MarkdownRemark","html":"<p>3D Flash engines seem to be popping up everywhere at the moment.  [Although I wouldn't call that a bad thing] - Each one tends to take a different approach, do a different demo to distinguish themselves.  Take this for example, a very impressive looking offering from Alternativa3D;</p>\n<img class=\"alignnone size-full wp-image-224\" title=\"alternativa\" src=\"/content/images/2008/05/alternativa.jpg\" alt=\"\" width=\"500\" height=\"280\" />\n<p>Papervision might have a lot of features and a large fan-base, but I have yet to see such a visually impressive demo [as this one].  And they've already sorted the first-person movement and collision detection.  It's also very cool that the demo seems to 'quickstart' - loading low-res textures to get you going as soon as possible, and streaming in higher res. ones while you play.</p>\n<img class=\"alignnone size-full wp-image-225\" title=\"texturing\" src=\"/content/images/2008/05/texturing.jpg\" alt=\"\" width=\"500\" height=\"280\" />\n<p>Progressive textures - how cool is that? (above)</p>\n<p>Of course, there's visual trickery going on here - the fantastic visuals are more down to excellent textures, rather than dynamic lighting or raytracing.  But that doesn't mean that the Alternativa3D team doesn't have that on their 'to-do' list...</p>\n<p>So, what are you waiting for?  Go check it out at <a title=\"Open link in a new window\" href=\"http://blog.alternativaplatform.com/en/\" target=\"_blank\">alternativaplatform.com</a>.</p>","frontmatter":{"title":"Alternativa3D - More 3D goodness from the Flash Player","date":"May 03, 2008","tags":["external-links","flash","papervision"]}}},"pathContext":{}}

